import { useEffect, useState } from "react";
import { Getprofile, createOnlinePayment } from "../../../../api/login/Login";
import { toast } from "react-toastify";
import Select from "react-select";

function OnlinePaymentRequest() {
  const [loader, setLoader] = useState(false);
  const [profileData, setProfileData] = useState(null);

  const [onlineData, setOnlineData] = useState({
    payment_from: "",
    username: "",
    mobile: "",
    email: "",
    address: "",
    area_pincode: "",
    account_no: "",
    method: "",
    amount: "",
    lat: "",
    long: "",
  });

  const [disabledFields, setDisabledFields] = useState({
    username: false,
    mobile: false,
    email: false,
    address: false,
    area_pincode: false,
    account_no: false,
  });

  useEffect(() => {
    const fetchProfile = async () => {
      try {
        const res = await Getprofile();
        const pdata = res?.data?.data || null;
        setProfileData(pdata);
      } catch (err) {
        console.error("Getprofile error:", err);
        toast.error("Failed to load profile");
      }
    };

    fetchProfile();

    navigator.geolocation.getCurrentPosition(
      (pos) =>
        setOnlineData((prev) => ({
          ...prev,
          lat: pos.coords.latitude,
          long: pos.coords.longitude,
        })),
      () => toast.warning("Location disabled, default applied")
    );
  }, []);

  useEffect(() => {
  if (!onlineData.payment_from || !profileData) return;

  if (onlineData.payment_from === "own") {
    setOnlineData((prev) => ({
      ...prev,
      username: profileData?.name || "",
      mobile: profileData?.mobile || "",
      email: profileData?.email || "",
      address: profileData?.presentAddr || "",
      area_pincode: profileData?.pinCode || "",
      account_no: profileData?.bankDetails?.bank_account_number || "",
    }));

    setDisabledFields({
      username: true,
      mobile: true,
      email: true,
      address: true,
      area_pincode: true,
      account_no: true,
    });
  }

  if (onlineData.payment_from === "OtherBank") {
    setOnlineData((prev) => ({
      ...prev,
      username: profileData?.name || "",
      mobile: profileData?.mobile || "",
      email: profileData?.email || "",
      address: profileData?.presentAddr || "",
      area_pincode: profileData?.pinCode || "",
      account_no: "",
    }));

    setDisabledFields({
      username: true,
      mobile: true,
      email: true,
      address: true,
      area_pincode: true,
      account_no: false, 
    });
  }
}, [onlineData.payment_from, profileData]);


  const validateEmail = (email) => {
    const regex =
      /^(?!.*\.\.)(?!.*\.$)[a-zA-Z0-9._%+-]+@[a-zA-Z0-9-]+(?:\.[A-Za-z]{2,})+$/;
    return regex.test(String(email || "").trim());
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setOnlineData((prev) => ({ ...prev, [name]: value }));
  };

  const handleSelectChange = (name, selected) => {
    setOnlineData((prev) => ({ ...prev, [name]: selected?.value || "" }));
  };

  const submitOnlinePayment = async () => {
    if (!onlineData.payment_from) return toast.error("Select payment source");
    if (!onlineData.username) return toast.error("Full Name required");
    if (!onlineData.mobile) return toast.error("Mobile required");
    if (!onlineData.email) return toast.error("Email required");
    if (!validateEmail(onlineData.email)) return toast.error("Invalid email format");
    if (!onlineData.address) return toast.error("Address required");
    if (!onlineData.area_pincode) return toast.error("Pincode required");
    if (!onlineData.account_no) return toast.error("Account Number required");
    if (!onlineData.method) return toast.error("Payment method required");
    if (!onlineData.amount) return toast.error("Amount required");

    setLoader(true);
    try {
      const res = await createOnlinePayment(onlineData);
      const result = res?.data;
      if (result?.success && result?.data?.url) {
        toast.success("Redirecting to payment...");
        setTimeout(() => window.open(result.data.url, "_blank"), 500);
      } else {
        toast.error(result?.message || "Payment Failed");
      }
    } catch (err) {
      console.error("createOnlinePayment error:", err);
      toast.error("Network Error");
    } finally {
      setLoader(false);
    }
  };

  const inputStyle = {
    height: "45px",
    borderRadius: "12px",
    backgroundColor: "var(--bs-primary3)",
    color: "var(--bs-primary2)",
    border: "1px solid var(--bs-primary2)",
    paddingLeft: "10px",
    fontSize: "14px",
    width: "100%",
  };

  const reactSelectStyles = {
    control: (p) => ({
      ...p,
      backgroundColor: "var(--bs-primary3)",
      color: "var(--bs-primary2)",
      border: "1px solid var(--bs-primary2)",
      borderRadius: "12px",
      height: "45px",
      minHeight: "45px",
      cursor: "pointer",
      boxShadow: "none",
      "&:hover": { border: "1px solid var(--bs-primary2)" },
    }),
    menu: (p) => ({ ...p, backgroundColor: "var(--bs-primary3)", border: "1px solid var(--bs-primary2)" }),
    option: (p, s) => ({
      ...p,
      backgroundColor: s.isFocused ? "#99999950" : "var(--bs-primary3)",
      color: "var(--bs-primary2)",
      borderBottom: "1px solid var(--bs-primary2)",
      cursor: "pointer",
    }),
    singleValue: (p) => ({ ...p, color: "var(--bs-primary2)" }),
    placeholder: (p) => ({ ...p, color: "var(--bs-primary2)", opacity: 0.6 }),
  };

  return (
    <div className="PageHeading">
      <h1>Online Payment Request</h1>

      <div className="ContentArea">
        <div
          className="card"
          style={{
            background: "var(--bs-primary1)",
            color: "var(--bs-primary2)",
            border: "1.5px solid var(--bs-primary2)",
            borderRadius: "16px",
            padding: "20px",
          }}
        >
          <div className="card-body">
            <form>
              <div
                className="form-row"
                style={{
                  border: "1px solid var(--bs-primary2)",
                  padding: "15px",
                  borderRadius: "16px",
                }}
              >
                <div className="form-group col-md-3">
  <label style={{ fontWeight: 700 }}>Add Amount From *</label>

  <Select
    value={
      onlineData.payment_from
        ? {
            value: onlineData.payment_from,
            label:
              onlineData.payment_from === "own"
                ? "Own Account"
                : "Other Bank Account",
          }
        : null
    }
    onChange={(selected) => handleSelectChange("payment_from", selected)}
    options={[
      { value: "own", label: "Own Account" },
      { value: "OtherBank", label: "Other Bank Account" },
    ]}
    styles={reactSelectStyles}
    placeholder="Select Source"
    isSearchable={false}
    isDisabled={false}   // ðŸ”¥ Always enabled
  />
</div>


                {onlineData.payment_from !== "" && (
                  <>
                    <div className="form-group col-md-3">
                      <label style={{ fontWeight: 700 }}>Full Name *</label>
                      <input
                        className="form-control"
                        name="username"
                        value={onlineData.username}
                        onChange={handleChange}
                        style={inputStyle}
                        placeholder="Enter Full Name"
                        disabled={disabledFields.username}
                      />
                    </div>

                    <div className="form-group col-md-3">
                      <label style={{ fontWeight: 700 }}>Mobile *</label>
                      <input
                        className="form-control"
                        name="mobile"
                        maxLength={10}
                        value={onlineData.mobile}
                        onChange={handleChange}
                        style={inputStyle}
                        placeholder="Enter Mobile"
                        disabled={disabledFields.mobile}
                      />
                    </div>

                    <div className="form-group col-md-3">
                      <label style={{ fontWeight: 700 }}>Email *</label>
                      <input
                        className="form-control"
                        name="email"
                        type="email"
                        value={onlineData.email}
                        onChange={handleChange}
                        style={inputStyle}
                        placeholder="Enter Email"
                        disabled={disabledFields.email}
                      />
                    </div>

                    <div className="form-group col-md-3">
                      <label style={{ fontWeight: 700 }}>Address *</label>
                      <input
                        className="form-control"
                        name="address"
                        value={onlineData.address}
                        onChange={handleChange}
                        style={inputStyle}
                        placeholder="Enter Address"
                        disabled={disabledFields.address}
                      />
                    </div>

                    <div className="form-group col-md-3">
                      <label style={{ fontWeight: 700 }}>Pincode *</label>
                      <input
                        className="form-control"
                        name="area_pincode"
                        maxLength={6}
                        value={onlineData.area_pincode}
                        onChange={handleChange}
                        style={inputStyle}
                        placeholder="Enter Pincode"
                        disabled={disabledFields.area_pincode}
                      />
                    </div>

                    <div className="form-group col-md-3">
                      <label style={{ fontWeight: 700 }}>Account Number *</label>
                      <input
                        className="form-control"
                        name="account_no"
                        maxLength={18}
                        value={onlineData.account_no}
                        onChange={handleChange}
                        style={inputStyle}
                        placeholder="Enter Account Number"
                        disabled={disabledFields.account_no}
                      />
                    </div>

                    <div className="form-group col-md-3">
                      <label style={{ fontWeight: 700 }}>Payment Method *</label>

                      <Select
                        value={
                          onlineData.method
                            ? { value: onlineData.method, label: onlineData.method.toUpperCase() }
                            : null
                        }
                        onChange={(selected) => handleSelectChange("method", selected)}
                        options={[
                          { value: "upi", label: "UPI" },
                          { value: "card", label: "Card" },
                          { value: "netbanking", label: "Net Banking" },
                          { value: "wallet", label: "Wallet" },
                        ]}
                        placeholder="Select Method"
                        styles={reactSelectStyles}
                        isSearchable={false}
                      />
                    </div>

                    <div className="form-group col-md-3">
                      <label style={{ fontWeight: 700 }}>Amount *</label>
                      <input
                        className="form-control"
                        name="amount"
                        type="number"
                        value={onlineData.amount}
                        onChange={handleChange}
                        style={inputStyle}
                        placeholder="Enter Amount"
                      />
                    </div>

                    <div className="form-group col-md-12 mt-2">
                      <p
                        style={{
                          fontSize: "13px",
                          color: "red",
                          fontWeight: "600",
                          marginTop: "5px",
                          textAlign: "center",
                        }}
                      >
                        Note: Enter the exact bank account number used for the payment.
                        The retailerâ€™s details and the bank account information must match â€” incorrect or mismatched details will result in the payment not being processed.
                        </p>
                    </div>

                    <div className="form-group col-md-12 text-center mt-3">
                      <button
                        type="button"
                        onClick={submitOnlinePayment}
                        disabled={
                          loader ||
                          !onlineData.username ||
                          !onlineData.mobile ||
                          !onlineData.email ||
                          !onlineData.address ||
                          !onlineData.area_pincode ||
                          !onlineData.account_no ||
                          !onlineData.method ||
                          !onlineData.amount
                        }
                        style={{
                          borderRadius: "50px",
                          padding: "12px 45px",
                          backgroundColor:
                            !loader &&
                            onlineData.username &&
                            onlineData.mobile &&
                            onlineData.email &&
                            onlineData.address &&
                            onlineData.area_pincode &&
                            onlineData.account_no &&
                            onlineData.method &&
                            onlineData.amount
                              ? "var(--bs-primary3)"
                              : "#777",
                          color: "var(--bs-primary2)",
                          border: "1px solid var(--bs-primary2)",
                          fontWeight: "bold",
                          fontSize: "16px",
                        }}
                      >
                        {loader ? "Processing..." : "Submit Request"}
                      </button>
                    </div>
                  </>
                )}
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
}

export default OnlinePaymentRequest;
